.contact{
	.tabs-nav{
		max-width: 800px;
		margin: 0 auto 50px;
		padding: 0;
	}
	.tabs-content{
		padding: 0;
		position: relative;
		.map{
			width: 100%;
			display: block;
			min-height: 650px;
		}
		.container{
			position: absolute;
			top: 85px;
			left: 50%;
			transform: translateX(-50%);
		}
		.location-block{
			width: 460px;
			.arrow{
				position: absolute;
				right: 10px;
				top: 30px;
				padding: 20px;
				transition: .3s;
				display: flex;
				align-items: center;
				justify-content: center;
				cursor: pointer;
				outline: none;
				border-radius: 10px;
				.icon{
					width: 20px;
					height: 20px;
				}
			}
			&__ul{
				> li{
					position: relative;
					> a{
						font-size: 15px;
						line-height: 200%;
						letter-spacing: -0.01em;
						color: #404041;
						font-weight: 400;
						span{
							display: block;
							font-weight: 600;
							font-size: 22px;
							line-height: 136%;
							letter-spacing: -0.01em;
							color: #404041;
							text-decoration: none;
							margin: 0;
						}
					}
				}
				> li.open{
					.arrow{
						transform: rotate(-180deg);
					}
				}
			}

			.drop{
				display: none;
				padding: 20px 30px;
				background: #F1F5F7;
				position: relative;
				left: -67px;
				bottom: -25px;
				right: -22px;
				width: calc(100% + (67px + 22px));
				border-top: 1px solid rgba(119, 124, 126, 0.2);
				li{
					padding: 0;
					border: 0;
					background: none;
					margin-bottom: 10px;
				}
				a{
					font-size: 16px;
					line-height: 120%;
					color: #404041;
					font-weight: 400;
					&:hover{
						text-decoration: underline;
					}
				}
				.time{
					margin: 20px 0;
				}
				.mail{
					text-decoration: underline;
					&:hover{
						text-decoration: none;
						text-decoration: none;
					}
				}
			}
		}

	}
	&--bg{
		background-color: #F1F5F6;
		padding: 80px 0;
		.form-opt{
			margin: 0 auto;
		}
	}
}